@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins, sans-serif;
}

body,
html {
  font-size: 16px;
  width: 100%;
  height: 100%;
}

.StripeElement {
  height: 40px;
  padding: 10px 12px;
  width: 320px;
  margin-bottom: 1rem;
  color: #32325d;
  background-color: white;
  margin-top: 1rem;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em,
    rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em,
    rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
  transition: box-shadow 150ms ease;
}

.stripeForm-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 2rem;
}

.StripeElement--focus {
  box-shadow: 0 1px 3px 0 #cfd7df;
}

.StripeElement--invalid {
  border-color: #fa755a;
}

.StripeElement--webkit-autofill {
  background-color: #fefde5 !important;
}

/* General Container + Header */
.hero-section-container {
  margin: 0 auto;
  margin-top: 1rem;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.hero-section-container h1 {
  font-size: 2rem;
  font-weight: 200;
}

.underline-text {
  font-size: 2rem;
  color: #6aa3a1;
  font-weight: 400;
}

/* Content Container */
.form-full-container {
  margin-top: 1rem;
  margin-bottom: 1rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  /* text-align: center; */
  width: 50rem;
  flex-wrap: wrap;
  position: relative;
  background-color: rgba(255, 255, 255, 0.8) !important;
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 40px 55px -7px;
}

.form-full-container-success {
  margin-top: 1rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 50rem;
  height: 20rem;
  position: relative;
  background-color: rgba(255, 255, 255, 0.738) !important;
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 40px 55px -7px;
}

/* Left Content */
.form-header-left {
  display: flex;
  flex-direction: column;
  justify-content: left;
  width: 25rem;
  padding: 1rem 1rem;
}

.form-header-left p {
  font-size: 0.8rem;
  font-weight: 250;
  line-height: 1.3rem;
  margin-bottom: 1.5rem;
}

.form-header-left-success {
  display: flex;
  /* position: relative; */
  flex-direction: column;
  justify-content: left;
  width: 30rem;
  padding: 1rem 1rem;
}

.form-header-left-success p {
  font-size: 0.8rem;
  font-weight: 200;
  line-height: 1.4rem;
  margin-bottom: 1rem;
}

.success-container-right {
  display: flex;
  flex-direction: column;
  width: 45rem;
}

.success-container-right img {
  width: 28rem;
  margin: 0 auto;
  box-shadow: 0px 9px 22px -2px #000000;
}

.form-container {
  display: flex;
  margin: 1rem auto;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  border: 1px solid transparent;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 12px 28px 0px,
    rgba(0, 0, 0, 0.1) 0px 2px 4px 0px,
    rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;
  background-color: white;
  border-radius: 4px;
  width: 23rem;
  padding: 1.5rem 0;
  /* position: relative; */
}

.form-element {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.form-element label {
  color: #6aa3a1;
  font-size: 0.9rem;
  font-weight: 200;
  margin-right: 0.5rem;
}

.stripe-label {
  margin-right: 0 !important;
}

.form-element input {
  color: black;
  font-size: 0.7rem;
  width: 12rem;
  font-weight: 300;
  border: none;
  border-bottom: 1px ridge rgba(0, 0, 0, 0.3);
}

.card-header {
  margin: 1rem 0;
  font-weight: 300;
  font-size: 1rem;
}

.security-container {
  margin-top: 2rem;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.stripe-logo {
  margin: 0 auto;
  display: flex;
  width: 10rem;
  flex: 0 0 50%;
}

.StripeElement {
  margin-bottom: 0;
}

.btn-submit-stripe {
  background-color: #41c1bd;
  color: white;
  height: 1.9rem;
  width: 8rem;
  border: none;
  border-radius: 14px;
  cursor: pointer;
  font-weight: 300;
  font-size: 0.8rem;
}

.terms-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  /* margin-bottom: 1rem; */
}

.checkbox_text {
  font-size: 0.6rem;
  width: 18rem;
}

.advice-security-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  /* margin-bottom: 1rem; */
}

.advice-security-text {
  font-size: 0.6rem;
  margin-bottom: 1rem;
  width: 20rem;
  color: rgb(146, 17, 17);
}

.charge-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  text-align: right;
  margin-top: 1rem;
  margin-bottom: 1rem;
  border-top: 1px solid #6aa3a1;
}

.charge-container p:not(:last-child) {
  font-size: 0.7rem;
  margin-top: 0.5rem;
  margin-right: 1.4rem;
  color: black;
}

.charge-container p {
  font-size: 0.7rem;
  margin-right: 1.4rem;
  color: black;
}

input[type="checkbox"] {
  width: 1rem;
}

.error-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 90%;
  margin: 1rem auto;
}

.error-message {
  color: rgb(152, 17, 17);
  font-size: 0.7rem;
}

.rimbo-sign {
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  align-items: baseline;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.rimbo-sign h4 {
  font-size: 1rem;
  font-weight: 400;
  margin-right: 1rem;
}

.rimbo-sign img {
  width: 5rem;
}

.rimbo-sign-success {
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  align-items: baseline;
  margin-top: 2rem;
}

.rimbo-sign-success h4 {
  font-size: 1rem;
  font-weight: 400;
  margin-right: 1rem;
}

.rimbo-sign-success img {
  width: 5rem;
}

::-webkit-input-placeholder {
  color: rgb(191, 191, 191);
  text-align: left;
}

:-ms-input-placeholder {
  color: rgb(191, 191, 191);
  text-align: left;
}

::placeholder {
  color: rgb(191, 191, 191);
  text-align: left;
}

/* Reset inputs shadow on click */
input[type="text"]:focus {
  outline: none;
  box-shadow: none;
}
input[type="number"]:focus {
  outline: none;
  box-shadow: none;
}
input[type="email"]:focus {
  outline: none;
  box-shadow: none;
}
input[type="tel"]:focus {
  outline: none;
  box-shadow: none;
}

input[type="file"]:focus {
  outline: none;
  box-shadow: none;
}

input[type="date"]:focus {
  outline: none;
  box-shadow: none;
}

input[type="checkbox"] {
  outline: none;
  box-shadow: none;
}

select:focus {
  outline: none;
  box-shadow: none;
}

.navbar-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 1rem;
}

.navbar-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.enso-image {
  width: 3rem;
  margin-right: 1rem;
}

.enso-logo-text-container {
  display: flex;
  flex-direction: column;
}

.enso-logo-text {
  margin-bottom: -0.5rem;
  font-size: 2rem;
  font-weight: 700;
}

.App {
  position: relative;
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App::before {
  content: "";
  background-image: url(/static/media/undraw_buy_house_560d.78214a2e.png);
  background-size: cover;
  background-repeat: no-repeat, repeat;
  background-position: center;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  opacity: 0.18;
  max-width: 100%;
  height: auto;
}

